import { useCallback, useEffect } from "react";
import styles from "./RightDrawer.module.css";
const RightDrawer = ({ onClose }) => {
  useEffect(() => {
    const scrollAnimElements = document.querySelectorAll(
      "[data-animate-on-scroll]"
    );
    const observer = new IntersectionObserver(
      (entries) => {
        for (const entry of entries) {
          if (entry.isIntersecting || entry.intersectionRatio > 0) {
            const targetElement = entry.target;
            targetElement.classList.add(styles.animate);
            observer.unobserve(targetElement);
          }
        }
      },
      {
        threshold: 0.15,
      }
    );

    for (let i = 0; i < scrollAnimElements.length; i++) {
      observer.observe(scrollAnimElements[i]);
    }

    return () => {
      for (let i = 0; i < scrollAnimElements.length; i++) {
        observer.unobserve(scrollAnimElements[i]);
      }
    };
  }, []);

  const onFrameClick = useCallback(() => {
    window.open("/draftshandpageafter-selectionmobile");
  }, []);

  const onFrame2Click = useCallback(() => {
    window.open("/draftshandpageafter-selectionmobile");
  }, []);

  return (
    <div className={styles.rightdrawer} data-animate-on-scroll>
      <h2 className={styles.drafts}>Drafts</h2>
      <img className={styles.dividerIcon} alt="" src="/divider.svg" />
      <div className={styles.rightdrawerChild} onClick={onFrameClick} />
      <div className={styles.rightdrawerItem} />
      <div className={styles.rightdrawerInner} onClick={onFrame2Click} />
    </div>
  );
};

export default RightDrawer;
