import { useMemo } from "react";
import styles from "./BottomFooter.module.css";
const BottomFooter = ({
  dimensions,
  dimensions1,
  dimensions2,
  dimensions3,
  propZIndex,
  propCursor,
  propCursor1,
  onButtonSecondaryWithIconLeClick,
  onButtonSecondaryWithIconLe1Click,
}) => {
  const frameDivStyle = useMemo(() => {
    return {
      zIndex: propZIndex,
    };
  }, [propZIndex]);

  const buttonSecondaryWithIconLeStyle = useMemo(() => {
    return {
      cursor: propCursor,
    };
  }, [propCursor]);

  const buttonSecondaryWithIconLe1Style = useMemo(() => {
    return {
      cursor: propCursor1,
    };
  }, [propCursor1]);

  return (
    <div className={styles.frameParent} style={frameDivStyle}>
      <div className={styles.buttonSecondaryWithIconleParent}>
        <div
          className={styles.buttonSecondaryWithIconle}
          style={buttonSecondaryWithIconLeStyle}
          onClick={onButtonSecondaryWithIconLeClick}
        >
          <div className={styles.startSpacing} />
          <img className={styles.plus24Outline} alt="" src={dimensions} />
          <div className={styles.label}>New Hand</div>
          <div className={styles.endSpacing} />
        </div>
        <div
          className={styles.buttonSecondaryWithIconle}
          style={buttonSecondaryWithIconLe1Style}
          onClick={onButtonSecondaryWithIconLe1Click}
        >
          <div className={styles.startSpacing} />
          <img className={styles.plus24Outline} alt="" src={dimensions1} />
          <div className={styles.label}>Saved hands</div>
          <div className={styles.endSpacing} />
        </div>
        <div className={styles.buttonSecondaryWithIconle}>
          <div className={styles.startSpacing} />
          <img className={styles.minus24Outline} alt="" src={dimensions2} />
          <div className={styles.label}>End Session</div>
          <div className={styles.endSpacing} />
        </div>
      </div>
      <div className={styles.textareafield}>
        <div className={styles.inputgroup}>
          <div className={styles.input}>
            <div className={styles.placeholder}>Tell me your hand</div>
            <img
              className={styles.paperPlane24Outline}
              alt=""
              src={dimensions3}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default BottomFooter;
