import { useEffect } from "react";
import MainHeader from "../components/MainHeader";
import AIResponseForm from "../components/AIResponseForm";
import AIResponseContainer from "../components/AIResponseContainer";
import BottomFooter from "../components/BottomFooter";
import styles from "./NewHandAfterHandInputPageMob1.module.css";
const NewHandAfterHandInputPageMob1 = () => {
  useEffect(() => {
    const scrollAnimElements = document.querySelectorAll(
      "[data-animate-on-scroll]"
    );
    const observer = new IntersectionObserver(
      (entries) => {
        for (const entry of entries) {
          if (entry.isIntersecting || entry.intersectionRatio > 0) {
            const targetElement = entry.target;
            targetElement.classList.add(styles.animate);
            observer.unobserve(targetElement);
          }
        }
      },
      {
        threshold: 0.15,
      }
    );

    for (let i = 0; i < scrollAnimElements.length; i++) {
      observer.observe(scrollAnimElements[i]);
    }

    return () => {
      for (let i = 0; i < scrollAnimElements.length; i++) {
        observer.unobserve(scrollAnimElements[i]);
      }
    };
  }, []);

  return (
    <div className={styles.newhandafterhandinputpagemob}>
      <MainHeader dimensions="/onlylogo8.svg" />
      <div className={styles.frameParent}>
        <div className={styles.pullrighticonWrapper}>
          <button className={styles.pullrighticon} data-animate-on-scroll>
            <div className={styles.pullrighticonChild} />
            <img
              className={styles.connection24Outline}
              alt=""
              src="/connection--24--outline6.svg"
            />
          </button>
        </div>
        <div className={styles.airesponsecardParent}>
          <AIResponseForm
            dimensions="/round-only-logo4.svg"
            dimensionsText="/approvetick--24--outline3.svg"
            dimensions1557="/history4.svg"
            sessionNameInputTop="14.63px"
            propCursor="unset"
          />
          <AIResponseContainer
            dimensionsText="/round-only-logo5.svg"
            dimensionsCode="/approvetick--24--outline4.svg"
            dimensions15Text="/pencil--24--outline1.svg"
            dimensions1557Text="/history5.svg"
          />
          <div className={styles.airesponsecard}>
            <img
              className={styles.roundOnlyLogo}
              alt=""
              src="/round-only-logo6.svg"
            />
            <div className={styles.yourHandIs}>
              Your hand is saved. Open the hand to review it.
            </div>
          </div>
          <BottomFooter
            dimensions="/plus--24--outline2.svg"
            dimensions1="/justify-alignment--24--outline4.svg"
            dimensions2="/minus--24--outline3.svg"
            dimensions3="/paper-plane--24--outline1.svg"
          />
          <div className={styles.leftAlignment24Outline}>
            <img className={styles.vectorIcon} alt="" src="/vector4.svg" />
            <div className={styles.editSession}>Edit Session</div>
          </div>
        </div>
        <div className={styles.pullrighticonContainer}>
          <button className={styles.pullrighticon1} data-animate-on-scroll>
            <div className={styles.pullrighticonItem} />
            <img
              className={styles.connection24Outline}
              alt=""
              src="/connection--24--outline7.svg"
            />
          </button>
        </div>
      </div>
    </div>
  );
};

export default NewHandAfterHandInputPageMob1;
