import { useEffect } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { Form } from "react-bootstrap";
import MainHeader from "../components/MainHeader";
import AIResponseForm from "../components/AIResponseForm";
import AIResponseContainer from "../components/AIResponseContainer";
import styles from "./NewHandAfterHandInputPageMob2.module.css";
const NewHandAfterHandInputPageMob2 = () => {
  useEffect(() => {
    const scrollAnimElements = document.querySelectorAll(
      "[data-animate-on-scroll]"
    );
    const observer = new IntersectionObserver(
      (entries) => {
        for (const entry of entries) {
          if (entry.isIntersecting || entry.intersectionRatio > 0) {
            const targetElement = entry.target;
            targetElement.classList.add(styles.animate);
            observer.unobserve(targetElement);
          }
        }
      },
      {
        threshold: 0.15,
      }
    );

    for (let i = 0; i < scrollAnimElements.length; i++) {
      observer.observe(scrollAnimElements[i]);
    }

    return () => {
      for (let i = 0; i < scrollAnimElements.length; i++) {
        observer.unobserve(scrollAnimElements[i]);
      }
    };
  }, []);

  return (
    <div className={styles.newhandafterhandinputpagemob}>
      <MainHeader dimensions="/onlylogo8.svg" />
      <div className={styles.frameParent}>
        <div className={styles.pullrighticonWrapper}>
          <button className={styles.pullrighticon} data-animate-on-scroll>
            <div className={styles.pullrighticonChild} />
            <img
              className={styles.connection24Outline}
              alt=""
              src="/connection--24--outline6.svg"
            />
          </button>
        </div>
        <div className={styles.airesponsecardParent}>
          <AIResponseForm
            dimensions="/round-only-logo7.svg"
            dimensionsText="/approvetick--24--outline5.svg"
            dimensions1557="/history6.svg"
          />
          <AIResponseContainer
            dimensionsText="/round-only-logo8.svg"
            dimensionsCode="/approvetick--24--outline6.svg"
            dimensions15Text="/pencil--24--outline2.svg"
            dimensions1557Text="/history7.svg"
          />
          <div className={styles.airesponsecard}>
            <img
              className={styles.roundOnlyLogo}
              alt=""
              src="/round-only-logo9.svg"
            />
            <div className={styles.yourHandIs}>
              Your hand is saved. Open the hand to review it.
            </div>
          </div>
          <div className={styles.airesponsecard1}>
            <img
              className={styles.roundOnlyLogo}
              alt=""
              src="/round-only-logo10.svg"
            />
            <div className={styles.yourHandIs}>
              How much is your stack after game?
            </div>
          </div>
          <div className={styles.frameGroup}>
            <div className={styles.buttonSecondaryWithIconleParent}>
              <div className={styles.buttonSecondaryWithIconle}>
                <div className={styles.startSpacing} />
                <img
                  className={styles.plus24Outline}
                  alt=""
                  src="/plus--24--outline1.svg"
                />
                <div className={styles.label}>New Hand</div>
                <div className={styles.endSpacing} />
              </div>
              <div className={styles.buttonSecondaryWithIconle}>
                <div className={styles.startSpacing} />
                <img
                  className={styles.plus24Outline}
                  alt=""
                  src="/justify-alignment--24--outline5.svg"
                />
                <div className={styles.label}>Saved hands</div>
                <div className={styles.endSpacing} />
              </div>
              <div className={styles.buttonSecondaryWithIconle}>
                <div className={styles.startSpacing} />
                <img
                  className={styles.minus24Outline}
                  alt=""
                  src="/minus--24--outline4.svg"
                />
                <div className={styles.label}>End Session</div>
                <div className={styles.endSpacing} />
              </div>
            </div>
            <Form.Group className={styles.textareafieldFormgroup}>
              <Form.Label>Amount</Form.Label>
              <Form.Control type="number" placeholder="Enter amount" />
            </Form.Group>
          </div>
          <div className={styles.leftAlignment24Outline}>
            <img className={styles.vectorIcon} alt="" src="/vector4.svg" />
            <div className={styles.editSession}>Edit Session</div>
          </div>
        </div>
        <div className={styles.pullrighticonContainer}>
          <button className={styles.pullrighticon1} data-animate-on-scroll>
            <div className={styles.pullrighticonItem} />
            <img
              className={styles.connection24Outline}
              alt=""
              src="/connection--24--outline8.svg"
            />
          </button>
        </div>
      </div>
    </div>
  );
};

export default NewHandAfterHandInputPageMob2;
