import { useState, useCallback, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import MainHeader from "../components/MainHeader";
import LeftDrawer from "../components/LeftDrawer";
import PortalDrawer from "../components/PortalDrawer";
import BottomFooter from "../components/BottomFooter";
import RightDrawer from "../components/RightDrawer";
import styles from "./AddHandPageMobile.module.css";
const AddHandPageMobile = () => {
  const navigate = useNavigate();
  const [isLeftDrawerOpen, setLeftDrawerOpen] = useState(false);
  const [isRightDrawerOpen, setRightDrawerOpen] = useState(false);

  const onButtonSecondaryWithIconLeClick = useCallback(() => {
    navigate("/newhandpagemobile");
  }, [navigate]);

  const onButtonSecondaryWithIconLe1Click = useCallback(() => {
    navigate("/allhandspagemobile");
  }, [navigate]);

  useEffect(() => {
    const scrollAnimElements = document.querySelectorAll(
      "[data-animate-on-scroll]"
    );
    const observer = new IntersectionObserver(
      (entries) => {
        for (const entry of entries) {
          if (entry.isIntersecting || entry.intersectionRatio > 0) {
            const targetElement = entry.target;
            targetElement.classList.add(styles.animate);
            observer.unobserve(targetElement);
          }
        }
      },
      {
        threshold: 0.15,
      }
    );

    for (let i = 0; i < scrollAnimElements.length; i++) {
      observer.observe(scrollAnimElements[i]);
    }

    return () => {
      for (let i = 0; i < scrollAnimElements.length; i++) {
        observer.unobserve(scrollAnimElements[i]);
      }
    };
  }, []);

  const openLeftDrawer = useCallback(() => {
    setLeftDrawerOpen(true);
  }, []);

  const closeLeftDrawer = useCallback(() => {
    setLeftDrawerOpen(false);
  }, []);

  const openRightDrawer = useCallback(() => {
    setRightDrawerOpen(true);
  }, []);

  const closeRightDrawer = useCallback(() => {
    setRightDrawerOpen(false);
  }, []);

  return (
    <>
      <div className={styles.addhandpagemobile}>
        <MainHeader dimensions="/onlylogo4.svg" />
        <div className={styles.frameParent}>
          <div className={styles.pullrighticonWrapper}>
            <button
              className={styles.pullrighticon}
              onClick={openLeftDrawer}
              data-animate-on-scroll
            >
              <div className={styles.pullrighticonChild} />
              <img
                className={styles.connection24Outline}
                alt=""
                src="/connection--24--outline4.svg"
              />
            </button>
          </div>
          <div className={styles.frameGroup}>
            <div className={styles.componentParent}>
              <div className={styles.component}>
                <div className={styles.iconParent}>
                  <img className={styles.icon} alt="" src="/icon.svg" />
                  <div className={styles.rules}>Rules</div>
                </div>
                <div className={styles.mayOccasionallyGenerateIncoWrapper}>
                  <div className={styles.mayOccasionallyGenerateContainer}>
                    <p className={styles.mayOccasionallyGenerate}>
                      May occasionally generate
                    </p>
                    <p className={styles.mayOccasionallyGenerate}>
                      incorrect information
                    </p>
                  </div>
                </div>
                <div className={styles.mayOccasionallyGenerateIncoWrapper}>
                  <div className={styles.mayOccasionallyGenerateContainer}>
                    May occasionally produce harmful instructions or biased
                    content
                  </div>
                </div>
                <div className={styles.mayOccasionallyGenerateIncoWrapper}>
                  <div className={styles.mayOccasionallyGenerateContainer}>
                    Limited knowledge of world and events after 2021
                  </div>
                </div>
              </div>
              <div className={styles.ellipseParent}>
                <div className={styles.frameChild} />
                <div className={styles.frameItem} />
                <div className={styles.frameItem} />
              </div>
            </div>
            <BottomFooter
              dimensions="/plus--24--outline.svg"
              dimensions1="/justify-alignment--24--outline.svg"
              dimensions2="/minus--24--outline.svg"
              dimensions3="/paper-plane--24--outline.svg"
              propZIndex="1"
              propCursor="pointer"
              propCursor1="pointer"
              onButtonSecondaryWithIconLeClick={
                onButtonSecondaryWithIconLeClick
              }
              onButtonSecondaryWithIconLe1Click={
                onButtonSecondaryWithIconLe1Click
              }
            />
            <div className={styles.leftAlignment24Outline}>
              <img className={styles.vectorIcon} alt="" src="/vector4.svg" />
              <div className={styles.editSession}>Edit Session</div>
            </div>
          </div>
          <div className={styles.pullrighticonContainer}>
            <button
              className={styles.pullrighticon1}
              onClick={openRightDrawer}
              data-animate-on-scroll
            >
              <div className={styles.pullrighticonItem} />
              <img
                className={styles.connection24Outline}
                alt=""
                src="/connection--24--outline5.svg"
              />
            </button>
          </div>
        </div>
      </div>
      {isLeftDrawerOpen && (
        <PortalDrawer
          overlayColor="rgba(113, 113, 113, 0.3)"
          placement="Left"
          onOutsideClick={closeLeftDrawer}
        >
          <LeftDrawer onClose={closeLeftDrawer} />
        </PortalDrawer>
      )}
      {isRightDrawerOpen && (
        <PortalDrawer
          overlayColor="rgba(113, 113, 113, 0.3)"
          placement="Right"
          onOutsideClick={closeRightDrawer}
        >
          <RightDrawer onClose={closeRightDrawer} />
        </PortalDrawer>
      )}
    </>
  );
};

export default AddHandPageMobile;
