import { useEffect } from "react";
import styles from "./MainHeader.module.css";
const MainHeader = ({ dimensions }) => {
  useEffect(() => {
    const scrollAnimElements = document.querySelectorAll(
      "[data-animate-on-scroll]"
    );
    const observer = new IntersectionObserver(
      (entries) => {
        for (const entry of entries) {
          if (entry.isIntersecting || entry.intersectionRatio > 0) {
            const targetElement = entry.target;
            targetElement.classList.add(styles.animate);
            observer.unobserve(targetElement);
          }
        }
      },
      {
        threshold: 0.15,
      }
    );

    for (let i = 0; i < scrollAnimElements.length; i++) {
      observer.observe(scrollAnimElements[i]);
    }

    return () => {
      for (let i = 0; i < scrollAnimElements.length; i++) {
        observer.unobserve(scrollAnimElements[i]);
      }
    };
  }, []);

  return (
    <nav className={styles.navbar}>
      <div className={styles.logoWName} data-animate-on-scroll>
        <img className={styles.onlylogoIcon} alt="" src={dimensions} />
        <div className={styles.onlynamelogo}>
          <div className={styles.racker}>Racker.</div>
        </div>
      </div>
      <div className={styles.bars24OutlineWrapper}>
        <div className={styles.bars24Outline}>
          <button className={styles.vectorWrapper} data-animate-on-scroll>
            <img
              className={styles.vectorIcon}
              alt=""
              src="/vector3.svg"
              data-animate-on-scroll
            />
          </button>
        </div>
      </div>
    </nav>
  );
};

export default MainHeader;
